<template>
  <article class="contactUs subPage">
    <header>
      <h2>聯絡我們</h2>
      <p>Contact Us</p>
    </header>
    <section class="wrapper style5">
      <div class="inner">
        <img src="@/assets/images/contact-us.jpg" alt="contact us">
        <h4><i class="icon fa-envelope" /> 聯絡我們</h4>
        <form method="post" action="#">
          <div class="row gtr-uniform">
            <div class="col-6 col-12-xsmall">
              <ValidationProvider rules="required" v-slot="{ errors }">
                <input v-model="formData.name" type="text" name="name" id="name" placeholder="姓名" />
                <small class="text-danger">{{ errors[0] }}</small>
              </ValidationProvider>
            </div>
            <div class="col-6 col-12-xsmall">
              <ValidationProvider rules="required|email" v-slot="{ errors }">
                <input v-model="formData.email" type="email" name="email" id="email" placeholder="電子郵件" />
                <small class="text-danger">{{ errors[0] }}</small>
              </ValidationProvider>
            </div>
            <div class="col-12">
              <textarea v-model="formData.message" name="message" id="message" placeholder="問題" rows="5"></textarea>
            </div>
            <div class="col-6 d-flex align-items-center">
              <span class="text-nowrap">驗證碼：</span>
              <Verify ref="Verify" :type="VerifyType"
                @success="onVerifySuccess"
                @error="onVerifyError"
                :showButton="false" />
            </div>
            <div class="col-12">
              <ul class="actions">
                <li><input @click="checkVerify" type="submit" value="送出" class="primary" /></li>
                <li><input @click="restHandler" type="reset" value="清除" /></li>
              </ul>
            </div>
          </div>
        </form>
      </div>
    </section>
  </article>
</template>

<script>
import { ValidationProvider, extend, localize } from 'vee-validate'
import { required, email } from 'vee-validate/dist/rules'
import TW from 'vee-validate/dist/locale/zh_TW.json'
import Verify from 'vue2-verify'
localize('zh_TW', TW)
extend('email', email)
extend('required', {
  ...required,
  message: '必填欄位！'
})

export default {
  name: 'ContactUs',
  components: {
    Verify,
    ValidationProvider
  },
  data () {
    return {
      formData: {
        name: '',
        email: '',
        message: ''
      },
      VerifyType: 1
    }
  },
  methods: {
    // 清除
    restHandler () {
      this.formData = this.$options.data.call(this).formData
    },
    // 檢查驗證碼
    checkVerify () {
      this.$refs.Verify.checkCode()
    },
    // 驗證碼正確
    onVerifySuccess () {
      alert('驗證碼正確！')
    },
    // 驗證碼錯誤
    onVerifyError (obj) {
      alert('驗證碼錯誤！')
      obj.refresh()
    }
  }
}
</script>

<style lang="scss">
.contactUs {
  .wrapper.style5 .inner img {
    margin-bottom: .8rem;
    border-radius: 3px;
  }
}
</style>
